@import url(https://fonts.googleapis.com/css2?family=Capriola&display=swap);
body {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
}

/* Here using -webkit, we make angle
    of 45 degree of falling star */
.sky {
  position: relative;
  width: 100%;
  height: 100%;
  transform: rotateZ(45deg);
}

/* Here we are making roundabout balls */
.star {
  position: absolute;
  opacity: 0.5;
  left: 50%;
  top: 50%;
  height: 2px;
  border-radius: 885px;
  background: linear-gradient(-45deg, #eef0f5, rgba(0, 0, 255, 0));

  -webkit-filter: drop-shadow(0 0 6px #eef1f8);
  filter: drop-shadow(0 0 6px #d7dff0);
  animation: tail 8000ms ease-in-out 0ms infinite,
    shooting 8000ms ease-in-out 0ms infinite;
}

/* Here we add before and after effect to star */
.star::before,
.star::after {
  content: "";
  position: absolute;
  top: calc(50% - 1px);
  right: 0;
  height: 2px;
  background: linear-gradient(
    -45deg,
    rgba(0, 0, 255, 0),
    #eaeef8,
    rgba(0, 0, 255, 0)
  );
  transform: translateX(50%) rotateZ(45deg);
  border-radius: 100%;
  animation: shining 8000ms ease-in-out 0ms infinite;
}
.star::after {
  transform: translateX(50%) rotateZ(-45deg);
}

/* Here we are adding location of each
    nth-child, animations, delays, and
    before and after effects to each
    and every balls(stars) */
.star:nth-child(1) {
  top: calc(50% - -119px);
  left: calc(50% - 43px);
  animation-delay: 7000ms;
}
.star:nth-child(1)::before,
.star:nth-child(1)::after {
  animation-delay: 7000ms;
}
.star:nth-child(2) {
  top: calc(50% - -43px);
  left: calc(50% - 37px);
  animation-delay: 10000ms;
}
.star:nth-child(2)::before,
.star:nth-child(2)::after {
  animation-delay: 10000ms;
}
.star:nth-child(3) {
  top: calc(50% - -40px);
  left: calc(50% - 222px);
  animation-delay: 12000ms;
}
.star:nth-child(3)::before,
.star:nth-child(3)::after {
  animation-delay: 12000ms;
}
.star:nth-child(4) {
  top: calc(50% - -29px);
  left: calc(50% - 113px);
  animation-delay: 12000ms;
}
.star:nth-child(4)::before,
.star:nth-child(4)::after {
  animation-delay: 12000ms;
}
.star:nth-child(5) {
  top: calc(50% - 146px);
  left: calc(50% - 112px);
  animation-delay: 8000ms;
}
.star:nth-child(5)::before,
.star:nth-child(5)::after {
  animation-delay: 8000ms;
}
.star:nth-child(6) {
  top: calc(50% - -108px);
  left: calc(50% - 160px);
  animation-delay: 8000ms;
}
.star:nth-child(6)::before,
.star:nth-child(6)::after {
  animation-delay: 8000ms;
}
.star:nth-child(7) {
  top: calc(50% - 52px);
  left: calc(50% - 72px);
  animation-delay: 9000ms;
}
.star:nth-child(7)::before,
.star:nth-child(7)::after {
  animation-delay: 9000ms;
}

/* This code will help to generate
    effect in tail of star */
@keyframes tail {
  0% {
    width: 0;
  }
  30% {
    width: 100px;
  }
  100% {
    width: 0;
  }
}

/* This code will generate shining
    effect in head of star */
@keyframes shining {
  0% {
    width: 0;
  }
  50% {
    width: 30px;
  }
  100% {
    width: 0;
  }
}

/* Here we are adding webkit-animation
    to head of star for shining */

/* Here we are adding webkit-animation
    to tail of star */

/* Here we are adding webkit-animation
    for shooting effect */

/* Here we make shooting effect */
@keyframes shooting {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(calc(100vh - 20px));
  }
}

